
* {
    padding: 0px;
    margin: 0px;
}
#myCanvas{
    width: 100%;
    height: 45vw ;
    background-color:gainsboro;
    display:block;
    margin: 0px;
}

.game-over-notify {
    position: fixed;
    display: none;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.5);
    align-items: center;
    justify-content: center;
    font-family: sans-serif;
    font-size: 40px;
    font-weight: bold;
    color: red;
  }